import React from 'react';
import Icons from './Icons';
import Click from './Click';

import './Square.css';

export default class Square extends React.Component {
  handleContextMenu = e => {
    e.preventDefault();
  }

  /* –ù–∞–∂–∞—Ç–∏–µ –õ–ö–ú –∏ –ü–ö–ú –æ–¥–Ω–æ–≤—Ä–µ–º–µ–Ω–Ω–æ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –±–µ–∑–æ–ø–∞—Å–Ω–æ–≥–æ –æ—Ç–∫—Ä—ã—Ç–∏—è —è—á–µ–µ–∫.
   * –†–∞–∑–¥–µ–ª–µ–Ω–∏–µ –Ω–∞ onMouseDown –∏ onMouseUp —Å–¥–µ–ª–∞–Ω–æ –¥–ª—è —Å–ª–µ–¥—É—é—â–µ–≥–æ:
   * –µ—Å–ª–∏ –±—ã–ª–∞ –Ω–∞–∂–∞—Ç–∞ –æ–¥–Ω–∞ –∏–∑ –∫–Ω–æ–ø–æ–∫ (1 - –õ–ö–ú, 2 - –ü–ö–ú), —Ç–æ –±—É–¥–µ—Ç –≤—ã–∑–≤–∞–Ω–æ —Å–æ–±—ã—Ç–∏–µ –æ–¥–Ω–æ–π –∏–∑ —ç—Ç–∏—Ö –∫–Ω–æ–ø–æ–∫,
   * –∏–Ω–∞—á–µ –±—É–¥–µ—Ç —Å—á–∏—Ç–∞—Ç—å—Å—è –Ω–∞–∂–∞—Ç–∏–µ –¥–≤—É—Ö –∫–Ω–æ–ø–æ–∫ (3).
   * –ì—Ä—É–±–æ –≥–æ–≤–æ—Ä—è, –∏–≥—Ä–æ–∫ –Ω–µ –ø—Ä–æ–∏–≥—Ä–∞–µ—Ç, –µ—Å–ª–∏ –¥–∞–∂–µ —Å–ª—É—á–∞–π–Ω–æ –Ω–∞–∂–º–µ—Ç –æ–±–µ –∫–Ω–æ–ø–∫–∏ –Ω–∞ –∫–ª–µ—Ç–∫—É, –≥–¥–µ –µ—Å—Ç—å –±–æ–º–±–∞. */

  handleMouseDown = e => {
    this.buttons = e.buttons;
  }

  handleMouseUp = e => {
    if (!this.buttons) return;

    switch (this.buttons) {
      case 1:
        this.props.onSquareClick(Click.openSquare, this.props);
        break;
      case 2:
        this.props.onSquareClick(Click.setMark, this.props);
        break;
      case 3:
        this.props.onSquareClick(Click.openNearSquares, this.props);
        break;
      default:
        break;
    }

    this.buttons = null;
  }

  handleTouchStart = e => {
    this._timeStamp = e.timeStamp;
  }

  handleTouchEnd = e => {
    e.preventDefault();

    const diff = e.timeStamp - this._timeStamp;

    if (!this.props.isOpened && diff < 500)
      this.props.onSquareClick(Click.openSquare, this.props);

    if (!this.props.isOpened && diff >= 500)
      this.props.onSquareClick(Click.setMark, this.props);

    if (this.props.isOpened)
      this.props.onSquareClick(Click.openNearSquares, this.props);
  }

  render() {
    const className = ['square'];
    let value = '';

    if (this.props.isOpened) {
      className.push('opened');

      if (this.props.withBomb)
        value = Icons.bomb;

      if (this.props.nearBombsCount)
        value = this.props.nearBombsCount;
    } else {
      if (this.props.isMarked)
        value = Icons.mark;
    }

    return (
      <div
        className={className.join(' ')}
        onContextMenu={this.handleContextMenu}
        onMouseDown={this.handleMouseDown}
        onMouseUp={this.handleMouseUp}
        onTouchStart={this.handleTouchStart}
        onTouchEnd={this.handleTouchEnd}
      >
        <div className="dummy"></div>
        <div className="value">
          {value}
        </div>
      </div>
    );
  }
}
