import Status from "./Status";

/**
 * @typedef Square –Ø—á–µ–π–∫–∞
 * @property {number} x –ö–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞ X
 * @property {number} y –ö–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞ Y
 * @property {boolean} withBomb –ï—Å—Ç—å –±–æ–º–±–∞
 * @property {number} nearBombsCount –°–∫–æ–ª—å–∫–æ —Ä—è–¥–æ–º –±–æ–º–±
 * @property {boolean} isOpened –û—Ç–∫—Ä—ã—Ç–∞ –ª–∏
 * @property {boolean} isMarked –ü–æ–º–µ—á–µ–Ω–∞ –ª–∏
 */

/**
 * –õ–æ–≥–∏–∫–∞ –∏–≥—Ä—ã "–°–∞–ø–µ—Ä"
 */
export default class Logic {
  /**
   * –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ–µ –ø–æ–ª–µ.
   * @param {number} width –®–∏—Ä–∏–Ω–∞
   * @param {number} height –í—ã—Å–æ—Ç–∞
   * @returns {Square[][]} –ü–æ–ª–µ
   */
  static initBoard(width, height) {
    return Array(+height).fill().map((_, y) =>
      Array(+width).fill().map((_, x) => ({
        x, y,
        withBomb: false,
        nearBombsCount: 0,
        isOpened: false,
        isMarked: false,
        })
      )
    )
  }

  /**
   * C–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞—Ç—å –±–æ–º–±—ã –Ω–∞ –ø–æ–ª–µ.
   * @param {Square[][]} board –ü–æ–ª–µ
   * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @param {number} bombsCount –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–æ–º–±
   * @returns {Square[][] | null} –ù–æ–≤–æ–µ –ø–æ–ª–µ
   */
  static generateBombs(board, square, bombsCount) {
    board = board.map(row => row.map(sqr => sqr));

    const cx = square.x
    const cy = square.y

    const width = board[0].length;
    const height = board.length;

    for (let i = 0; i < +bombsCount; i++) {
      const x = Math.floor(Math.random() * width);
      const y = Math.floor(Math.random() * height);

      let canAdd = true;

      if (board[y][x].withBomb)
        canAdd = false;

      const r = Math.random() >= 0.75 ? 1 : 0

      if (cx - r <= x && x <= cx + r && cy - r <= y && y <= cy + r)
        canAdd = false;

      if (canAdd)
        board[y][x].withBomb = true;
      else
        i--;
    }

    for (const row of board) {
      for (const square of row) {
        if (square.withBomb)
          continue;

        let count = 0;

        doWithNearSquares(board, square, sqr => {
          if (sqr.withBomb)
            count++;
        })

        if (count)
          square.nearBombsCount = count;
      }
    }

    return board
  }

  /**
   * –û—Ç–∫—Ä—ã—Ç—å —è—á–µ–π–∫—É –∏, –ø–æ –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç–∏,
   * –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ —Ä—è–¥–æ–º —Å –Ω–µ–π, –µ—Å–ª–∏ –æ–Ω–∏ –ø—É—Å—Ç—ã–µ.
   * @param {Square[][]} board –ü–æ–ª–µ
   * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {Square[][] | null} –ù–æ–≤–æ–µ –ø–æ–ª–µ
   */
  static openSquare(board, square) {
    if (square.isOpened)
      return null;

    board = board.map(row => row.map(sqr => sqr));
    square = board[square.y][square.x];

    openSquareRec(board, square);

    if (square.isOpened && square.withBomb)
      board.forEach(row => row.forEach(sqr => sqr.isOpened = true));

    return board;
  }

  /**
   * –ü–æ—Å—Ç–∞–≤–∏—Ç—å –∏–ª–∏ —Å–Ω—è—Ç—å –º–µ—Ç–∫—É –Ω–∞ –∑–∞–∫—Ä—ã—Ç—É—é —è—á–µ–π–∫—É.
   * @param {Square[][]} board –ü–æ–ª–µ
   * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {Square[][] | null} –ù–æ–≤–æ–µ –ø–æ–ª–µ
   */
  static setMark(board, square) {
    if (square.isOpened)
      return null;

    board = board.map(row => row.map(sqr => sqr));
    square = board[square.y][square.x];

    square.isMarked = !square.isMarked;

    return board;
  }

  /**
   * –û—Ç–∫—Ä—ã—Ç—å —Å–æ—Å–µ–¥–Ω–∏–µ —è—á–µ–π–∫–∏, –µ—Å–ª–∏ —Å—Ä–µ–¥–∏ –Ω–∏—Ö –Ω–µ—Ç –±–æ–º–±.
   * @param {Square[][]} board –ü–æ–ª–µ
   * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {Square[][] | null} –ù–æ–≤–æ–µ –ø–æ–ª–µ
   */
  static openNearSquares(board, square) {
    let closedCount = 0;
    let isBombsNear = false; 

    doWithNearSquares(board, square, sqr => {
      if (!sqr.isOpened && !sqr.isMarked)
        closedCount++;
      else
        return;

      if (sqr.withBomb)
        isBombsNear = true;
    });

    if (!closedCount || isBombsNear)
      return null;

    board = board.map(row => row.map(sqr => sqr));

    doWithNearSquares(board, square, sqr => {
      if (!sqr.isOpened && !sqr.isMarked)
        openSquareRec(board, sqr);
    });

    return board;
  }

  /**
   * –ü–æ–ª—É—á–∏—Ç—å —Å—Ç–∞—Ç—É—Å –∏–≥—Ä—ã
   * @param {Square[][]} board –ü–æ–ª–µ
   * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
   * @returns {Status} –°—Ç–∞—Ç—É—Å –∏–≥—Ä—ã
   */
  static getStatus(board, square) {
    let isWin = true;

    for (const row of board) {
      for (const square of row) {
        if (!square.isOpened && !square.withBomb)
          isWin = false;
      }

      if (!isWin)
        break;
    }

    const isLose = square.isOpened && square.withBomb;

    return (
      isLose ? Status.lose :
      isWin  ? Status.win  :
      Status.running
    );
  }

  /**
   * –ü–æ–∫–∞–∑–∞—Ç—å –≤ –∫–æ–Ω—Å–æ–ª–∏ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –±–æ–º–± –Ω–∞ –ø–æ–ª–µ.
   * @param {Square[][]} board –ü–æ–ª–µ
   */
  static logBoard(board) {
    console.log(
      board.map(row => (
        row.map(square => {
          if (square.withBomb)
            return 'Q';

          if (square.nearBombsCount)
            return String(square.nearBombsCount);

          return ' ';
        })
      ))
    );
  }
}

/**
 * –° –ø–æ–º–æ—â—å—é —Ä–µ–∫—É—Ä—Å–∏–∏ –æ—Ç–∫—Ä—ã—Ç—å –≤—Å–µ –ø—É—Å—Ç—ã–µ —è—á–µ–π–∫–∏
 * –æ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–π –¥–æ —è—á–µ–µ–∫ —Å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ–º —Ä—è–¥–æ–º –Ω–∞—Ö–æ–¥—è—â–∏—Ö—Å—è –±–æ–º–±.
 * @param {Square[][]} board –ü–æ–ª–µ
 * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
 */
function openSquareRec(board, square) {
  if (square.isOpened || square.isMarked)
    return;

  square.isOpened = true;

  if (square.withBomb || square.nearBombsCount)
    return;

  doWithNearSquares(board, square, sqr => (
    openSquareRec(board, sqr)
  ));
};

/**
 * @callback NearSquareCallback
 * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
 */

/**
 * –°–¥–µ–ª–∞—Ç—å —á—Ç–æ-—Ç–æ —Å –±–ª–∏–∂–∞–π—à–∏–º–∏ –æ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–π —è—á–µ–π–∫–∞–º–∏ 
 * @param {Square[][]} board –ü–æ–ª–µ
 * @param {Square} square –í—ã–±—Ä–∞–Ω–Ω–∞—è —è—á–µ–π–∫–∞
 * @param {NearSquareCallback} callback –ö–æ–ª–±—ç–∫
 */
function doWithNearSquares(board, square, callback) {
  const { x, y } = square;

  for (let dy = -1; dy <= 1; dy++) {
    for (let dx = -1; dx <= 1; dx++) {
      if (!(board[y + dy] && board[y + dy][x + dx]))
        continue;

      const sqr = board[y + dy][x + dx];

      if (sqr.x === x && sqr.y === y)
        continue;

      callback(sqr);
    }
  }
}
